@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Cormorant+Upright:wght@300;400;500;600;700&family=Open+Sans:wght@300;400;500;600;700&display=swap');

:root {
  --font-base: 'Cormorant Upright', serif;
  --font-alt: 'Open Sans', sans-serif;
  
  --color-golden: #DCCA87;
  --color-black: #0C0C0C;
  --color-gray: #545454;
  --color-crimson: #F5EFDB;
  --color-grey: #AAAAAA;
  --color-white: #FFFFFF;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

.primary-btn{
  @apply text-sm block mx-auto bg-amber-100 text-black font-bold px-3 py-2 rounded-md;
}

body{
  background-color: black;
  font-family: var(--font-base);
}
section{
  margin: 10em 0em;
}

*::-webkit-scrollbar{
  width: 1vw;
}
*::-webkit-scrollbar-track{
  background-color: rgba(255, 255, 255, 0.342);
}
*::-webkit-scrollbar-thumb{
  background-color: rgba(255, 255, 255, 0.651);
  border-radius: 5px;
}
*::-webkit-scrollbar-thumb:hover{
  background-color: white;
  transform:scale(120%);
}
